.popup__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 36px 36px;
  height: 100%;
  flex-grow: 1;
}

@media screen and (max-width: 416px) {
  .popup__content {
    padding-right: 22px;
    padding-left: 22px;
    padding-bottom: 25px;
  }
}
